// api.js
//  AI API 

// 
const ModelConfigManager = {
  // 
  MODEL_TYPES: {
    O1: 'o1',
    GPT: 'gpt',
    DEEPSEEK: 'deepseek',
    // 
  },

  // 
  getModelType(model) {
    if (model.startsWith('o1-')) return this.MODEL_TYPES.O1;
    if (model.startsWith('deepseek-')) return this.MODEL_TYPES.DEEPSEEK;
    return this.MODEL_TYPES.GPT;
  },

  // 
  buildRequestBody(model, messages, functions, function_call) {
    const modelType = this.getModelType(model);
    
    // 
    const compressedMessages = messages.map(msg => ({
      ...msg,
      content: msg.content.replace(/\s+/g, ' ').trim()
    }));

    // 
    const compressedFunctions = functions ? functions.map(fn => ({
      ...fn,
      description: fn.description.replace(/\s+/g, ' ').trim()
    })) : undefined;
    
    switch (modelType) {
      case this.MODEL_TYPES.O1:
        return {
          model,
          messages: compressedMessages.map(msg => ({
            role: msg.role === 'system' ? 'user' : msg.role,
            content: msg.content
          })),
          temperature: 1,
          max_tokens: 8000,
        };
      
      case this.MODEL_TYPES.DEEPSEEK:
        return {
          model,
          messages: compressedMessages,
          response_format: {
            type: 'json_object'
          },
          temperature: 0.5,
          max_tokens: 8000,
        };
      
      case this.MODEL_TYPES.GPT:
      default:
        return {
          model,
          messages: compressedMessages,
          functions: compressedFunctions,
          function_call,
          temperature: 0.5,
          max_tokens: 8000,
        };
    }
  },

  // 
  handleResponse(model, response, terms = null) {
    const modelType = this.getModelType(model);
    const message = response.choices[0].message;

    switch (modelType) {
      case this.MODEL_TYPES.O1:
        try {
          // 
          if (typeof message.content === 'object') {
            return { functionResult: message.content };
          }

          //  content  JSON
          const jsonMatch = message.content.match(/```json\s*([\s\S]*?)\s*```/);
          if (!jsonMatch) {
            //  JSON  content
            try {
              const parsedContent = JSON.parse(message.content);
              // 
              if (parsedContent.terms && parsedContent.fixedDescriptions) {
                return { functionResult: parsedContent };
              }
              //  description 
              const processedContent = this._processMainStructureContent(parsedContent);
              return { functionResult: processedContent };
            } catch {
              throw new Error('AI  JSON');
            }
          }

          //  JSON
          const jsonStr = jsonMatch[1].replace(/\n/g, ' ').replace(/\\"/g, '"');
          const parsedJson = JSON.parse(jsonStr);
          // 
          if (parsedJson.terms && parsedJson.fixedDescriptions) {
            return { functionResult: parsedJson };
          }
          //  description 
          const processedJson = this._processMainStructureContent(parsedJson);
          return { functionResult: processedJson };
        } catch (error) {
          console.error(' o1 :', error);
          throw new Error(`AI : ${error.message}`);
        }

      case this.MODEL_TYPES.DEEPSEEK:
        try {
          if (!message.content) {
            throw new Error('DeepSeek ');
          }
          const parsedContent = JSON.parse(message.content);
          // 
          if (parsedContent.terms && parsedContent.fixedDescriptions) {
            return { functionResult: parsedContent };
          }
          //  description 
          const processedContent = this._processMainStructureContent(parsedContent);
          return { functionResult: processedContent };
        } catch (error) {
          console.error(' DeepSeek :', error);
          throw new Error(`DeepSeek : ${error.message}`);
        }

      case this.MODEL_TYPES.GPT:
      default:
        if (!message.function_call || !message.function_call.arguments) {
          throw new Error('AI  function_call ');
        }
        return { functionResult: JSON.parse(message.function_call.arguments) };
    }
  },

  //  description 
  _processMainStructureContent(content) {
    const processNode = (node) => {
      if (Array.isArray(node)) {
        return node.map(item => {
          if (item.description) {
            const processedItem = { ...item };
            processedItem.content = typeof item.description === 'object' ? 
              Object.entries(item.description)
                .map(([key, value]) => {
                  if (Array.isArray(value)) {
                    return `${key}:\n${value.map(v => `- ${v}`).join('\n')}`;
                  }
                  return `${key}: ${value}`;
                })
                .join('\n') :
              item.description;
            delete processedItem.description;
            return processedItem;
          }
          return item;
        });
      } else if (typeof node === 'object' && node !== null) {
        if (node.description) {
          //  description 
          const processedNode = { ...node };
          if (typeof node.description === 'object') {
            processedNode.content = Object.entries(node.description)
              .map(([key, value]) => {
                if (Array.isArray(value)) {
                  return `${key}:\n${value.map(v => `- ${v}`).join('\n')}`;
                }
                return `${key}: ${value}`;
              })
              .join('\n');
          } else {
            processedNode.content = node.description;
          }
          delete processedNode.description;
          
          // 
          for (const [key, value] of Object.entries(processedNode)) {
            if (key !== 'content' && typeof value === 'object' && value !== null) {
              processedNode[key] = processNode(value);
            }
          }
          return processedNode;
        } else if (Object.keys(node).length > 0) {
          const processedObj = {};
          for (const [key, value] of Object.entries(node)) {
            processedObj[key] = processNode(value);
          }
          return processedObj;
        }
      }
      return node;
    };

    // 
    const result = {};
    for (const [key, value] of Object.entries(content)) {
      if (typeof value === 'object' && value !== null) {
        result[key] = processNode(value);
      } else {
        result[key] = value;
      }
    }
    return result;
  }
};

export async function callAIAPI(apiUrl, apiKey, model, messages, functions, function_call) {
  const body = JSON.stringify(ModelConfigManager.buildRequestBody(model, messages, functions, function_call));

  const response = await fetch(apiUrl, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      Authorization: `Bearer ${apiKey}`,
    },
    body: body,
  });

  if (!response.ok) {
    throw new Error(`HTTP error! status: ${response.status}`);
  }

  const data = await response.json();
  return ModelConfigManager.handleResponse(model, data);
}

// ,
function buildMessages(model, systemRolePrompt, userPrompt, functionParams) {
  const modelType = ModelConfigManager.getModelType(model);

  switch (modelType) {
    case ModelConfigManager.MODEL_TYPES.O1:
      //  o1- , systemRolePrompt  userPrompt , functionParams 
      let functionPrompt = '';
      if (functionParams) {
        functionPrompt = `:\n${JSON.stringify(functionParams, null, 2)}`;
      }
      return [
        {
          role: 'user',
          content: `${systemRolePrompt}\n\n${userPrompt}\n\n${functionPrompt}`,
        },
      ];

    case ModelConfigManager.MODEL_TYPES.DEEPSEEK:
      // DeepSeek  JSON 
      let deepseekPrompt = '';
      if (functionParams) {
        // 
        if (functionParams.terms && functionParams.fixedDescriptions) {
          deepseekPrompt = `
 JSON 
1. fixedDescriptions  node2 
2. 
3. 


{
  "terms": {
    "node1": "",
    "node2": ["1", "2"],
    ...
  },
  "fixedDescriptions": {
    "1": "1xxxxxxxxxxxx",
    "2": "2xxxxxxxxxxxx"
  },
  ...
}


${JSON.stringify(functionParams, null, 2)}`;
        } else {
          deepseekPrompt = ` JSON \n${JSON.stringify(functionParams, null, 2)}`;
        }
      }
      return [
        {
          role: 'system',
          content: systemRolePrompt
        },
        {
          role: 'user',
          content: `${userPrompt}\n\n${deepseekPrompt}`
        }
      ];

    case ModelConfigManager.MODEL_TYPES.GPT:
    default:
      // 
      return [
        {
          role: 'system',
          content: systemRolePrompt,
        },
        {
          role: 'user',
          content: userPrompt,
        },
      ];
  }
}

// 
export async function generateNewMainStructure(apiUrl, apiKey, model, userContent, config) {
  const { prompts, functionCalls, systemRolePrompt } = buildConfigFunctions(config);

  const prompt = prompts.generateMainStructurePrompt(userContent);

  const messages = buildMessages(model, systemRolePrompt, prompt, functionCalls.mainStructureFunction.parameters);

  if (model.startsWith('o1-')) {
    return await callAIAPI(apiUrl, apiKey, model, messages);
  } else {
    const functions = [functionCalls.mainStructureFunction];
    const function_call = { name: functionCalls.mainStructureFunction.name };
    return await callAIAPI(apiUrl, apiKey, model, messages, functions, function_call);
  }
}

//  prompts  functionCalls
function buildConfigFunctions(config) {
  if (!config || !config.terms) {
    throw new Error('');
  }

  const { terms, fixedDescriptions, systemRolePrompt } = config;

  //  terms  fixedDescriptions  prompts  functionCalls
  const prompts = {
    generateMainStructurePrompt: (userContent) => `
# 
"${userContent}"${terms.node1}

# 
1. 
   - ${terms.node2}MECE
   - 
   - ${terms.node2}

2. 
   - ${terms.node2}${terms.node2ComplexItems.join('')}3-5${terms.node3}
   - ${terms.node3}2-4${terms.node4}
   - 

JSON`,

    adjustMainStructurePrompt: (currentDesign, adjustments) =>
      `${terms.node1}:
\`\`\`json
${JSON.stringify(currentDesign, null, 2).replace(/\s+/g, ' ').replace(/\n/g, ' ')}
\`\`\`
:
${adjustments}
${terms.node1}`,

    generateDetailPrompt: (
      nodeIndexes,
      currentDesign,
      existingSectionsPrompt,
      nodeTitle,
      subNodeTitle
    ) => `
${terms.node1}:
${JSON.stringify(currentDesign, null, 2).replace(/\s+/g, ' ').replace(/\n/g, ' ')} 

# 
${existingSectionsPrompt}

${terms.mainStructure}${terms.outline},${terms.mainStructure} ${nodeIndexes.node3} ${terms.node3} "${nodeTitle}"  ${nodeIndexes.node4} ${terms.node4} "${subNodeTitle}" ${terms.detail}

# 
1. 
   - ${terms.node4}
   - ${terms.node4}
   - ${terms.node1}

2. 
   - 
   - 2-3
   - 

3. 
   - 
   - 
   - 

4. 
   - Markdown
   - 
   - 

`,

    adjustDetailPrompt: (
      currentContent,
      adjustments,
      nodeIndexes,
      currentDesign,
      existingSectionsPrompt,
      nodeTitle,
      subNodeTitle
    ) => `
${terms.mainStructure}${terms.outline}:
${JSON.stringify(currentDesign, null, 2).replace(/\s+/g, ' ').replace(/\n/g, ' ')}

# 
${existingSectionsPrompt}

${terms.mainStructure}${terms.outline},${terms.mainStructure} ${nodeIndexes.node3 || ''} ${terms.node3} "${nodeTitle || ''}"  ${nodeIndexes.node4 || ''} ${terms.node4} "${subNodeTitle || ''}" ${terms.detail}

# 
${currentContent}

# 
${adjustments || ''}

# 
1. 
   - 
   - 
   - ${terms.node1}
   - 

2. 
   - 
   - 
   - 
   
Markdown`
  };

  //  terms  functionCalls
  const functionCalls = {
    mainStructureFunction: {
      name: 'generate_main_structure',
      description: `,${terms.node1}`,
      parameters: {
        type: 'object',
        properties: {
          [terms.node1]: {
            type: 'object',
            properties: terms.node2.reduce((acc, node2Name) => {
              if (terms.node2ComplexItems.includes(node2Name)) {
                //  node2  detailFlag
                acc[node2Name] = {
                  type: 'array',
                  items: {
                    type: 'object',
                    properties: {
                      [terms.title]: {
                        type: 'string',
                        description: fixedDescriptions[terms.node3],
                      },
                      [terms.content]: {
                        type: 'array',
                        items: { type: 'string' },
                        description: `${terms.node3}${terms.content}`,
                      },
                      [terms.detailFlag]: {
                        type: 'boolean',
                        description: fixedDescriptions[terms.detailFlag],
                      },
                    },
                    required: [terms.title, terms.content, terms.detailFlag],
                  },
                  description: fixedDescriptions[node2Name],
                };
              } else if (Array.isArray(fixedDescriptions[node2Name])) {
                //  node2 
                acc[node2Name] = {
                  type: 'array',
                  items: { type: 'string' },
                  description: fixedDescriptions[node2Name],
                };
              } else {
                //  node2 
                acc[node2Name] = {
                  type: 'string',
                  description: fixedDescriptions[node2Name],
                };
              }
              return acc;
            }, {}),
            required: terms.node2,
          },
        },
        required: [terms.node1],
      },
    },

    detailFunction: {
      name: 'generate_detail',
      description: `${terms.mainStructure}${terms.title},${terms.node4}`,
      parameters: {
        type: 'object',
        properties: {
          nodeIndexes: {
            type: 'object',
            properties: {
              node3: {
                type: 'integer',
                description: `${terms.node3}`,
              },
              node4: {
                type: 'integer',
                description: `${terms.node4}`,
              },
            },
            required: ['node3', 'node4'],
          },
          [terms.title]: {
            type: 'string',
            description: `${terms.node4}${terms.title}`,
          },
          [terms.detail]: {
            type: 'string',
            description: fixedDescriptions[terms.node5],
          },
          [terms.type]: {
            type: 'string',
            description: `,${terms.node4}`,
            enum: [terms.sectionDetailType],
          },
        },
        required: [
          'nodeIndexes',
          terms.title,
          terms.detail,
          terms.type,
        ],
      },
    },
  };

  return { prompts, functionCalls, systemRolePrompt };
}

